































































































import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import * as api from '@/api'

@Component
export default class Header extends Vue {
  showMenu = false
  data = null as any

  async created () {
    this.data = await api.getSingle('menu')
  }

  @Watch('$route')
  onRouteChange () {
    this.showMenu = false
  }
}
